
html,
.container,
.imgbox {
  width: 100%;
}
body {
  margin: 0;
  background: #f6f8fa;
}
.container {
  width: 100%;
}
.container .content {
  width: 100%;
  padding-top: 0.1rem;
}
.container .content .item {
  margin-left: 0.3rem;
  height: 1.105rem;
  width: 7.2rem;
  border-bottom: 0.01rem solid #e2e9ef;
}
.container .content .item .left_txt {
  width: 6rem;
  height: 0.62rem;
  float: left;
  margin-top: 0.3rem;
}
.container .content .item .left_txt .first {
  width: 100%;
  font-size: 0.26rem;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  color: #36425b;
  line-height: 0.35rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.container .content .item .left_txt .last {
  font-size: 0.24rem;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  color: #36425b;
  line-height: 0.35rem;
}
.container .content .item .right_txt {
  float: right;
  margin-right: 0.29rem;
  font-size: 0.24rem;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  line-height: 1.105rem;
}
.container .content .isshow {
  position: relative;
  top: 40%;
  width: 100%;
  text-align: center;
  font-family: PingFangSC-Medium;
  font-size: 0.24rem;
  color: #333;
  letter-spacing: 0;
}